<template>
  <v-container fluid grid-list-xl>
    <div>
      <h1 class="display-1 mb-3">Родители</h1>
    </div>

    <v-layout flex-child wrap>
      <v-flex xs12 md8 class="border">
        <v-flex class="white">
         
          <v-layout row wrap>
            <v-flex xs12 sm6 >
              <label>Возраст родителей</label>
              <v-layout>
                <v-flex py-0 >
              <v-text-field
                class="border"
                hide-details
                solo
                flat
                label="От"
                v-model="inputs.parentAge.from"
              ></v-text-field>
            </v-flex>
            <v-flex py-0 >
              <v-text-field
                class="border"
                hide-details
                solo
                flat
                v-model="inputs.parentAge.to"
                label="До"
              ></v-text-field>
              </v-flex>
              </v-layout>
            </v-flex>
            


            <v-flex xs12 sm6 >
              <label >Возраст ребенка</label>
               <v-layout>
                <v-flex py-0 >
              <v-text-field
                class="border"
                hide-details
                solo
                flat
                label="От"
                v-model="inputs.childAge.from"
              ></v-text-field>
            </v-flex>
            <v-flex py-0 >
              <v-text-field
                class="border"
                hide-details
                solo
                flat
                v-model="inputs.childAge.to"
                label="До"
              ></v-text-field>
            </v-flex>
              </v-layout>
            </v-flex>
          </v-layout>


          
          

          <v-layout wrap>
            <v-flex xs12 sm6>
              <label>Пол</label>
              <v-select
                class="border"
                hide-details
                solo
                flat
                label="Выберите пол"
                :items="selects.sex.items"
                v-model="selects.sex.selected"
                item-text="title"
                item-value="id"
              ></v-select>
            </v-flex>
            <v-flex xs12 sm6>
              <label>Семейное положение</label>
              <v-select
                class="border"
                hide-details
                solo
                flat
                label="Выберите семейное положение"
                :items="selects.status.items"
                v-model="selects.status.selected"
                item-text="title"
                item-value="id"
              ></v-select>
            </v-flex>
</v-layout>

          <!-- <v-divider class="my-3"></v-divider> -->

<v-layout row wrap>
            <v-flex xs12 sm6 mt-3>
              <v-checkbox
                class="mt-0"
                height='6px'
                :label="`Родители только с фотографией`"
                v-model="checkboxes.hasPhoto"
              ></v-checkbox>
            </v-flex>
          

            <v-flex xs12 sm3>
              <v-autocomplete
                v-model="selects.country.selected"
                :loading="selects.country.loading"
                :items="selects.country.items"
                :search-input.sync="selects.country.search"
                hide-no-data
                item-text="title"
                item-value="id"
                label="Страна"
                class="border"
                hide-details
                solo
                flat
              ></v-autocomplete>
            </v-flex>

            <v-flex xs12 sm3>
              <v-autocomplete
                :disabled="!selects.country.selected"
                v-model="selects.city.selected"
                :loading="selects.city.loading"
                :items="selects.city.items"
                :search-input.sync="selects.city.search"
                hide-no-data
                item-text="title"
                item-value="id"
                label="Город"
                class="border"
                hide-details
                solo
                flat
              >
                <template v-slot:no-data>
                  <v-list-tile>
                    <v-list-tile-title>
                      Search for your favorite
                      <strong>Cryptocurrency</strong>
                    </v-list-tile-title>
                  </v-list-tile>
                </template>
                <!-- <template v-slot:selection="{ item, selected }">
        <div class="subheading font-weight-light">{{item.title}}</div>
                </template>-->
                <template v-slot:item="{ item }">
                  <v-list-tile-avatar
                    color="indigo"
                    class="headline font-weight-light white--text"
                  >{{ item.title.charAt(0) }}</v-list-tile-avatar>
                  <v-list-tile-content>
                    <v-list-tile-title v-text="item.title"></v-list-tile-title>
                    <v-list-tile-sub-title v-text="item.region"></v-list-tile-sub-title>
                  </v-list-tile-content>
                </template>
              </v-autocomplete>
            </v-flex>

            
          </v-layout>

          <v-divider class="my-4"></v-divider>

          <label>Название задачи</label>
          <v-layout wrap>
            <v-flex xs12 sm6 md8 pt-0>
              <v-text-field 
                class="border" 
                v-model="inputs.taskTitle"
                flat 
                solo 
                label="Любое название (для себя)" 
                hide-details
              ></v-text-field>
            </v-flex>

            <v-flex xs12 sm6 md4 pt-0>
              <v-btn
                class="mt-0"
                style="height:50px;             
            font-size:20px; 
            text-transform:none; 
            background: linear-gradient(160deg,#4f555e,#4f555e,#5c6e68,#7f6b67); 
            color:white"
                block
                flat
                hide-details
                @click="getUsers"
                :loading="!answer"
              >
                <v-icon style="margin-right: 5px">mdi-play</v-icon>
                <label>Начать поиск</label>
              </v-btn>
            </v-flex>
          </v-layout>
        </v-flex>
      </v-flex>

      <v-flex xs12 md4 pt-0>
        <v-flex style="background:#e7fbed;font-size:13px">
          <h1>Описание</h1>
          <p>
            В поиске пользователей можно искать людей по полу,
            городу, возрасту, тех, у кого день рождения, семейному положению,
            наличию детей, а так же производить поиск их родственников,
            вторых половинок и детей.
            <br />
            <br />1. Введите параметры поиска людей
            <br />
            <br />2. Задайте понятное описание задачи, чтобы не забыть, кто у Вас собирается.
            <br />
            <br />3. Нажмите "Добавить задачу" и перейдите к следующей.
            <br />
            <br />При указании возраста, города, семейного положения выдача может
            отличаться от выдачи ВКонтакте, т.к. в базу попадают только те,
            кто не скрыл возраст, семейное положение, город настройками приватности.
          </p>
        </v-flex>
      </v-flex>

    </v-layout>
  </v-container>
</template>

<style scoped>
.border {
  border: 1px solid #d7d7d7;
}
</style>

<script>
export default {
  data() {
    return {
      search: null,
      calendar: {
        from: new Date().toISOString().substr(0, 10),
        to: new Date().toISOString().substr(0, 10)
      },
      checkboxes:{
        hasPhoto:true
      },
      inputs: {
        childAge: {
          from: null,
          to: null
        },
        parentAge:{
          from: null,
          to: null
        }
      },
      selects: {
        sex: {
          items: [
            { title: "Любой", id: 0 },
            { title: "Женский", id: 1 },
            { title: "Мужской", id: 2 }
          ],
          selected: null
        },
        status: {
          items: [
            { title: "Не женат / Не замужем", id: 1 },
            { title: "Есть друг / Есть подруга", id: 2 },
            { title: "Помолвлен / Помолвлена", id: 3 },
            { title: "Женат / Замужем", id: 4 },
            { title: "Всё сложно", id: 5 },
            { title: "В активном поиске", id: 6 },
            { title: "Влюблен / Влюблена", id: 7 },
            { title: "В гражданском браке", id: 8 }
          ],
          selected: null
        },
        country: {
          loading: false,
          items: [],
          selected: null,
          search: null
        },
        city: {
          loading: false,
          items: [],
          selected: null,
          search: null
        }
      },
      answer: "Ответ тут",
      value: null
    };
  },
  methods: {
    getParents() {
      if (!this.selects.city.selected) return;
      let obj = {
        childAgeFrom: +this.inputs.childAge.from,
        childAgeTo: +this.inputs.childAge.to,
        parentAgeFrom: +this.inputs.parentAge.from,
        parentAgeTo: +this.inputs.parentAge.to,
        sex: this.selects.sex.selected,
        city: this.selects.city.selected,
        country: this.selects.country.selected,
        hasPhoto: +this.checkboxes.hasPhoto,
        status: this.selects.status.selected
      };
      this.answer = "";
      this.$http.post("http://89.254.230.243:3000/getParents", obj).then(res => {
        this.answer = res.body;
      });
    },
    getCities(v) {
      if (!this.selects.country.selected) return;
      this.selects.city.loading = true;
      this.$http
        .post("http://89.254.230.243:3000/getCities", {
          q: v
        })
        .then(res => {
          this.selects.city.items = res.body.items;
          this.selects.city.loading = false;
        });
    },
    getCountries(v) {
      this.selects.country.loading = true;
      this.$http
        .post("http://89.254.230.243:3000/getCountries", {
          q: v
        })
        .then(res => {
          this.selects.country.items = res.body.items;
          this.selects.country.loading = false;
        });
    }
  },
  // computed:{
  //   cities () {
  //       this.selects.city.ids = {}
  //       this.selects.city.items.forEach(x=>this.selects.city.ids[x.title] = x.id)
  //     return this.selects.city.items.map(x=>x.title + (x.area ? ' | ' + x.area : '') + (x.region?' | ' + x.region:'') )
  //   },
  //   countries () {
  //      this.selects.country.ids = {}
  //       this.selects.country.items.forEach(x=>this.selects.country.ids[x.title] = x.id)
  //     return this.selects.country.items.map(x=>x.title)
  //   }
  // },
  watch: {
    "selects.city.search": function(val) {
      val && val !== this.selects.city.selected && this.getCities(val);
    },
    "selects.country.search": function(val) {
      val && val !== this.selects.country.selected && this.getCountries(val);
    }
  }
};
</script>
